export default {
	data() {
		return {
			detail:[],
		}
	},
	onLoad(e) {
		var t=this;	
		uni.showLoading();
		t.urlRequest.urlRequest(t.func.workPlanDetail,{plan_id:e.id},function(res){
			t.detail=res.data.status_list[0];
		})
		uni.hideLoading();
	},
	methods: {
		
	}
}
